body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

.pic {
    border-radius: 50%;
    width: 3rem;
    height:  3rem;
    border: 2px solid #62e2c0;
    margin: 0.5rem 0 0.5rem 1rem;
}

.header {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    position: fixed;
    width: 100%;
    top: 0;
    background: white;
    z-index: 1;
}

.name {
    margin-left: 1rem;
    color: #62e2c0;
    font-weight: bold;
}
.input-wrapper{
    position: fixed;
    bottom:0;
    width:100%;
    height: 3.5rem;
    border-top: solid 1px #62e2c0;
    display: -webkit-flex;
    display: flex;
}

.input {
    width: 100%;
    height: 100%;
    padding-left: 1rem;
    font-size: 1rem;
    border: none;
}

.input__arrow {
    width: 3rem;
    cursor: pointer;
    background: white;
    border: none;
    position: relative;
}

.input__arrow img {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 14px;
    height: 14px;
}

.messages-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 1rem 2rem 1rem 1rem;
    width: 100%;
    list-style:none;
    margin: 4rem 0 3.5rem 0;
}

.message {
    margin: 5px 0;
    display: -webkit-flex;
    display: flex;
}

.message--bot {
    -webkit-align-self: flex-start;
            align-self: flex-start;
}

.message--user {
    -webkit-align-self: flex-end;
            align-self: flex-end;
}

.message__username {
    color: gray;
    font-size: 12px;
    padding-bottom: 4px;
    display:  -webkit-flex;
    display:  flex;
    -webkit-align-items: baseline;
            align-items: baseline;
}

.message__username .message__date {
    font-size: 10px;
}

.bot-image {
    border-radius: 50%;
    width: 22px;
    height:  22px;
    border: 1px solid #62e2c0;
    margin-right: 1rem;
    margin-top: 20px;
    -webkit-align-self: center;
            align-self: center;
}

.message--bot .message__username .message__date {
    margin-left: 3px;
    color: #475277;
}

.message--user .message__username .message__date {
    margin-right: 3px;
    color: #475277;
}

.message--user .message__username {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    color: #a5acff;
}

.message--bot .message__username {
    text-align: left;
    color: #62e2c0;
}

.message--user .message__username {
    text-align: right;
}

.message__text {
    padding: 10px;
    max-width: 400px;
    position: relative;
    word-break: break-word;
    text-align: left;
}

.message--bot .message__text {
    background-color: #ddfde8;
}

.message--bot .message__text::before {
    content: '';
    width: 0;
    height: 0;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-right:6px solid #ddfde8;
    position: absolute;
    left: -6px;
    top: calc(50% - 6px);
}

.message--user .message__text {
    background-color: #e7e9ff;
}

.message--user .message__text::after {
    content: '';
    width: 0;
    height: 0;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-left: 6px solid #e7e9ff;
    position: absolute;
    right: -6px;
    top: calc(50% - 6px);
}

@media only screen and (max-width: 768px) {
    .bot-image {
        display: none;
    }
}
